
.invoice-input {
  background-color: whitesmoke;
  border: none;
  border-bottom: 1px solid lightgray;
  font-size: 1rem;
  padding-top: 13px;
  margin-bottom: 9px;
}
.invoice-button--edit {
  color: cornflowerblue;
}
.invoice-button--remove,
.invoice-button--cancel {
  color: crimson;
}
.invoice-button--confirm {
  color: limegreen;
}



