
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
.invoice {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  margin: auto;
  list-style-type: none;
  width: 1024px;
  border-radius: 1px;
  background-color: whitesmoke;
  padding: 10px 20px;
  -webkit-box-shadow: 2px 0px 10px 0.1;
          box-shadow: 2px 0px 10px 0.1;
}
.invoice-record,
.invoice-total,
.invoice-header {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
.invoice-header {
  text-transform: uppercase;
  font-weight: bold;
}
.invoice-name {
  width: 50%;
}
.invoice-count,
.invoice-price {
  width: 22%;
  text-align: right;
}
.invoice-controls {
  color: lightgray;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  font-size: 20px;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  line-height: 100%;
  padding-bottom: 25px;
  padding-top: 15px;
  text-align: center;
  width: 60px;
}
.invoice-controls,
.invoice-name,
.invoice-count,
.invoice-price {
  padding: 15px;
  margin: 10px;
}
.invoice-name--total,
.invoice-count--total,
.invoice-price--total {
  font-weight: bold;
}
.invoice-button--add {
  color: cornflowerblue;
}
.record-transition-enter-active,
.record-transition-leave-active {
  -webkit-transition: all .25s;
  transition: all .25s;
}
.record-transition-enter,
.record-transition-leave-to {
  opacity: 0;
}




